<template>
  <div class="box">
    <LayoutBox one="新增活动">
      <h4>商品介绍</h4>
      <el-form ref="form" label-width="100px" :model="form" :rules="rules" size="small">
        <el-form-item label="秒杀标题" prop="secondsKillName">
          <el-input v-model="form.secondsKillName" style="width: 454px" maxlength="30" show-word-limit />
        </el-form-item>
        <el-form-item label="活动时间" prop="secondsStartTime">
          <el-date-picker v-model="timeDate" type="date" placeholder="选择日期" />
          <el-time-select v-model="time" :picker-options="{ start: '06:00', step: '04:00', end: '22:00' }" placeholder="选择时间" />
        </el-form-item>
        <!-- <el-form-item label="持续时间" prop="whenLength">
          <el-input v-model="form.whenLength" style="width: 454px" />
        </el-form-item> -->
        <el-form-item label="活动描述" prop="activeDescribes">
          <el-input v-model="form.activeDescribes" style="width: 454px" />
        </el-form-item>
        <el-form-item label="折扣率" prop="discount">
          <el-input v-model.number="form.discount" style="width: 454px">
            <template slot="append"><span>%</span></template>
          </el-input>
        </el-form-item>
      </el-form>
    </LayoutBox>
    <el-card v-if="blUpdata" class="box-card">
      <h4>已关联商品</h4>
      <el-table :data="oldGoodsAndActivity" style="width: 100%">
        <el-table-column prop="name" label="商品名称" />
        <el-table-column prop="shopId" label="供应商" />
        <el-table-column prop="originalPrice" label="销售价（元）" />
        <el-table-column label="限购量">
          <template slot-scope="scope">
            <el-input v-if="oldUpdateIndex === scope.$index" v-model="scope.row.limited" />
            <p v-else>{{ scope.row.limited }}</p>
          </template>
        </el-table-column>
        <el-table-column prop="date" label="操作" width="150">
          <template slot-scope="scope">
            <el-button v-if="oldUpdateIndex === scope.$index" type="text" @click="onOldSave">完成</el-button>
            <el-button v-else type="text" @click.native.prevent="onOldUpdate(scope)">编辑</el-button>
            <el-button type="text">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="goodsTotle" @current-change="handleCurrentChange" />
    </el-card>
    <el-card class="box-card">
      <h4>新增关联商品</h4>
      <el-table :data="goodsAndActivity" style="width: 100%">
        <el-table-column prop="name" label="商品名称" />
        <el-table-column prop="originalPrice" label="销售价（元）" />
        <el-table-column label="限购量">
          <template slot-scope="scope">
            <el-input v-if="updateIndex === scope.$index" v-model.number="scope.row.limited" type="number" />
            <p v-else>{{ scope.row.limited }}</p>
          </template>
        </el-table-column>
        <el-table-column prop="date" label="操作" width="150">
          <template slot-scope="scope">
            <el-button v-if="updateIndex === scope.$index" type="text" @click="onSave(scope)">完成</el-button>
            <el-button v-else type="text" @click.native.prevent="onUpdate(scope)">编辑</el-button>
            <el-button type="text" @click="onDelete(scope)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="add_goods_btn">
        <el-button class="add_goods" type="text" icon="el-icon-plus" @click.native.prevent="onAddGoods">添加商品</el-button>
      </div>
      <el-divider />
      <div class="btn_group">
        <el-button v-if="blUpdata" class="inquire" type="success" size="medium" @click="save_btn">保存</el-button>
        <el-button v-else class="inquire" type="success" size="medium" @click="submit_btn">提交</el-button>
        <router-link to="/super/market/bargains/list"><el-button size="medium" style="width: 112px">取消</el-button></router-link>
      </div>
    </el-card>
    <el-dialog title="提示" :visible.sync="dialogVisible" fullscreen>
      <AddGoodsCmp ref="AddGoodsCmp" :activityTypes="0" />
      <span slot="footer" class="dialog-footer">
        <el-button round @click="dialogVisible = false">取 消</el-button>
        <el-button type="danger" round @click="addAll()">添 加</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import AddGoodsCmp from '../../add_goods_cmp/index'
import { MIXINS_SUPER_BARGAINS_AND_TEAM_BUY_FORMRULES } from '@/mixins/form.js'
import { MIXINS_SUPER_BARGAINS_AND_TEAM_BUY_ADDALL_EVENT } from '@/mixins/event.js'
import { MIXINS_BARGAINS_AND_TEAM_BUY_APIINSERTMERACTIVITY } from '@/mixins/api.js'
import { MIXINS_SUPER_BARGAINS_AND_TEAM_BUY_DATA } from '@/mixins/data.js'
import { MIXINS_BARGAINS_AND_TEAM_BUY_CREATED } from '@/mixins/created.js'

export default {
  name: 'AddBargains',

  components: {
    LayoutBox, AddGoodsCmp
  },

  mixins: [
    MIXINS_SUPER_BARGAINS_AND_TEAM_BUY_FORMRULES,
    MIXINS_SUPER_BARGAINS_AND_TEAM_BUY_ADDALL_EVENT,
    MIXINS_BARGAINS_AND_TEAM_BUY_APIINSERTMERACTIVITY,
    MIXINS_SUPER_BARGAINS_AND_TEAM_BUY_DATA,
    MIXINS_BARGAINS_AND_TEAM_BUY_CREATED,
  ],

  watch: {
    timeDate (nowVal) { // 活动时间日期
      if (this.time) {
        this.form.secondsStartTime = new Date(nowVal.toLocaleString('chinese', { hour12: false }).split(' ')[0] + ' ' + this.time)
      }
    },
    time (nowVal) { // 活动时间时分
      if (this.timeDate) {
        this.form.secondsStartTime = new Date(this.timeDate.toLocaleString('chinese', { hour12: false }).split(' ')[0] + ' ' + nowVal)
      }
    }
  },

  created () {

  },

  methods: {
    // 点击提交
    async submit_btn() {
      if (this.goodsAndActivity.length < 1) {
        this.$message.error('请先选择添加商品')
        return
      }
      if (this._formRules()) {
        const res = await this.apiInsertSmallSeckill()
        if (res.data.success) {
          this.$message.success('成功发布活动')
          this.$router.push('/super/market/bargains/list')
        } else {
          this.$message.error(res.data.msg)
        }
      }
    },
    // 点击保存
    async save_btn() {
      if (this._formRules()) {
        const res = await this.apiInsertSmallSeckill()
        if (res.data.success) {
          this.$message.success('更新活动成功')
          this.$router.push('/super/market/bargains/list')
        } else {
          this.$message.error(res.data.msg)
        }
      }
    },

    // api------------------------------------------
    // 添加秒杀活动
    async apiInsertSmallSeckill () {
      const data = {
        shopId: localStorage.shopId,
        samllProdDtos: this.goodsAndActivity,
        ...this.form,
        secondsStopTime: new Date(this.form.secondsStartTime.getTime() + (this.form.whenLength * 3600 * 1000)),
        // secondsStartTime: new Date('2020/12/16 15:57:00'),
        // secondsStopTime: new Date('2020/12/16 16:33:00'),
      }
      return await this.$http.post('/woho/activity/smallsuperSeckill/insertSmallSeckill', data)
    }
    
  }
}
</script>

<style lang="scss" scoped>
  $themColor: #5bd9be;

  .box{
    min-width: 1100px;
    .time_group{
      display: flex;
    }
    .box-card{
      width: 100%;
      margin-top: 15px;
      .add_goods_btn{
        width: 100%;
        text-align: center;
        .add_goods{
          color: $themColor;
        }
      }
      .btn_group{
        width: 100%;
        text-align: center;
      }
    }
  }
</style>
